.root {
  display: grid;
  grid-template-columns: 33% 33% 1fr;
  justify-items: center;
}

.outer {
  display: grid;
  align-content: center;
}

.wrapper {
  height: 180px;
  width: 360px;
  margin-block-start: 75px;
  display: grid;
  grid-template-columns: 60% 1fr;
  align-content: center;
  justify-content: center;
  box-sizing: border-box;
}

.ticket-bkgd {
  display: grid;
  grid-template-columns: 80% 1fr;
  height: 180px;
  width: 360px;
  border-radius: 12px;
  transform-origin: left;
  transition: all 0.3s ease;
  z-index: 2;
}

.wrapper:hover > .ticket-bkgd {
  transform: scaleX(0.6);
}

.wrapper:hover > .date-graphic {
  transform: translateX(-60%);
  opacity: 0;
}

.wrapper:hover > .graphic {
  transform: translateX(-48%);
}

.graphic {
  position: absolute;
  height: 150px;
  margin-top: 15px;
  margin-left: 105px;
  z-index: 3;
  transition: all 0.24s ease;
}

.date-graphic {
  grid-column: 2;
  width: 54px;
  height: 70px;
  text-align: center;
  padding: 0 10px;
  background-color: #fafafa;
  border-radius: 12px;
  transition: 0.14s ease-in;
  justify-self: flex-end;
  margin-top: 12px;
  margin-right: 12px;
  z-index: 3;
}

.month, .day {
  font-size: 18px;
  line-height: 0.8;
  margin: 6px 0;
}

.month {
  margin-bottom: 0px;
}

.day {
  font-size: 24px;
}

hr {
  color: black;
  margin: 6px;
}

.info {
  position: absolute;
  margin-left: 215px;
  margin-top: 5px;
  width: 135px;
  font-family: 'Comfortaa', cursive;
  font-size: 10px;
  text-align: center;
  line-height: 1;
}

.info p {
  padding: 0;
  margin: 5px;
}

#full-date {
  font-size: 15px;
  font-weight: 900;
  margin-bottom: 12px;
}

.btn {
  position: absolute;
  display: grid;
  align-items: center;
  grid-template-columns: 55% 1fr;
  width: 90px;
  margin-left: 240px;
  margin-top: 120px;
  padding: 5 1;
  border-radius: 8px;
  border-color: #353535;
  outline: #00000000;
  font-size: 9px;
}

.btn:hover {
  border-color: #0a0a0a;
}

.btn:active {
  background-color: #dadada;
  color: #000000;
}
.start-from {
  grid-column: 1;
  text-align: left;
}

.cost {
  grid-column: 2;
  font-size: 15px;
}

@font-face {
  font-family: 'Comfortaa';
  font-weight: 700;
  src: local('Comfortaa'), url(./Comfortaa-VariableFont_wght.ttf) format('opentype');
}